<template>
	<div>
		<div class="login-phone">
	      <input type="password" placeholder="" v-model="pass" maxlength="20" ref="password"> 
	      <span class="login-errror" :class="{fadeIn:errorPass}" v-show="errorPass"></span>
	    </div>
	    <input class="login-btn" type="button" name="" value="" @click="login">
        <p class="login-another" @click="checkNav"><span></span></p>
        <section class="login-popup" v-show="showPasswordDialog">
	        <!---->
	        <div class="login-failure">
	            <div class="login-failure-in">
	                <p class="login-tip"></p>
	                <p class="login-by-code"></p>
	                <p class="login-set-password"></p>
	            </div>
	            <p class="login-choose clearfix">
	                <span @click="closePasswordDialog"></span>
	                <span class="active" @click="checkNav"></span>
	            </p>
	        </div>
	    </section>
	</div>
</template>
<script>
import MD5 from 'crypto-js/md5';
import hmac from 'crypto-js/hmac-md5';
import Utf8 from 'crypto-js/enc-utf8';
import Base64 from 'crypto-js/enc-base64';

	export default {
		name:'pass',
		data () {
		    return {
		      pass:"",
		      errorPass:false,
		      showPasswordDialog:false, //
		      errorPasswordCount:0
		    }
		},
		mounted(){
			// 100 13522641774 000000
		  },
		methods:{
			passwordToMD5(passwordWord){
				var password = passwordWord;
				var passwordMD5 = MD5(password);  //
				var passwordHash = hmac(passwordMD5.toString(),sessionStorage.dataToken); //
				var passwordBase64 = Base64.stringify(Utf8.parse(passwordHash));  //utf-8base64
				return passwordBase64;
			},
			checkTel(){
		      var telExp = /^(1(3|4|5|7|8)[0-9]{1}\d{8})$/;
		        if(telExp.test(this.$parent.telephone)){
		           this.$parent.telError = false;
		         }else{
		           this.$parent.telError = true;
		           setTimeout(()=>{
		           		this.$parent.telError = false;
		           },1500);
		           this.$parent.$refs.telephone.focus();
		           return false;
		         }
		         return true;
		    },
			checkPass(){
				var passExp = /^[a-zA-Z0-9]+$/;
		    	if(passExp.test(this.pass)){
		    		this.errorPass = false;
		    	}else{
		    		this.errorPass = true;
		    		setTimeout(()=>{
		           		this.errorPass = false;
		           },1500);
		    		this.$refs.password.focus();
		    		return false;
		    	}
		    	return true;
			},
		    login(){
		    	if(!this.checkTel()) return "";
				if(!this.checkPass()) return "";
		        var data = {
		        	dataToken:sessionStorage.dataToken,
		            phone:this.$parent.telephone,//
		            password:this.passwordToMD5(this.pass)
		        };
		        this.$http({
		            url:"passport/login",
		            method:"GET",
		            params:data
		        }).then(function (response) {
		            sessionStorage.token = response.body.data.token;
		            this.passportCheck();
		          },function(error){
		          	console.log(error);
		          	if(error.body.code == 4011){
		          		this.errorPasswordCount++;
		          	}
		          	if(this.errorPasswordCount>=3){
		          		this.showPasswordDialog = true;
		          		return false;
		          	}
		          }).catch(function (error) {

		          }).finally(function(){
		          	 this.getDataToken();
		          });;
		    },
		    passportCheck(){ //
				this.$http({
		            url:"dealerInfo/idCardAuth?token="+sessionStorage.token,
		            method:"GET"
		        }).then(function (response) {
		        	var code = response.body.data["auth_status"];
		        	//return false;
		        	if(code == 1){ //
		        		this.$router.push('/index');
		        	}else if(code == 3){ //
		        		this.$router.push('/authResult');
		        	}else{
		        		this.$router.push('/auth');
		        	} 
		        	
		        	// //
		        },function(){

		        });
		    },
		    checkNav(){
		    	 this.$router.push('code'); //
		    },
		    closePasswordDialog(){
		    	this.showPasswordDialog = false;
		    },
		    getDataToken(){ //dataToken
		    	this.$http({
		            url:"passport/key/notApp",
		            method:"GET"
		        }).then(function (response) {
		            sessionStorage.dataToken = response.body.data.dataToken;
		          }).catch(function (error) {
		          	console.log(error);
		            console.log("");
		          });
		    }
	    }
	}
</script>


